export default {
  name: 'create-brick-modal',

  props: {
    isOpen: {
      type: Boolean,
      required: true
    }
  },

  data () {
    return {
      formData: {
        text: ''
      }
    }
  },

  methods: {
    close () {
      this.$emit('close')
    },

    save () {
      this.$emit('add-brick', JSON.parse(JSON.stringify(this.formData)))
    }
  }
}



