import BrickRow from './brick-row'
import CreateBrickModal from './create-brick-modal'

export default {
  name: 'bricks',
  components: {BrickRow, CreateBrickModal},

  data () {
    return {
      isBrickModalOpen: false,

      groupedBricks: [],

      bricks: [{
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }, {
        text: 'just a brick'
      }]
    }
  },

  created () {
    this.setupRows()
  },

  methods: {
    setupRows () {
      let rowLength = 6
      let currentRow = []

      this.bricks.forEach(brick => {
        currentRow.push(brick)

        if (currentRow.length >= rowLength) {
          this.groupedBricks.push(currentRow)
          currentRow = []
          rowLength = rowLength === 6 ? 5 : 6
        }
        console.log(currentRow)
      })

      if (currentRow.length > 0) {
        this.groupedBricks.push(currentRow)
      }

      this.groupedBricks = this.groupedBricks.reverse()
    },

    addBrick (brick) {
      if (this.groupedBricks[0].length >= 6) {
        this.groupedBricks.unshift([])
      }

      this.groupedBricks[0].push(brick)
    }
  }
}



